import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import styled from "styled-components";
import {
  ModalOverlay,
  Purple,
  BtnWrapper,
  CloseButton,
  SetButton,
} from "./SetMyModal";

function ReservateModal({ isReservateOpen, closeReservateModal, cliniccall }) {
  //  
  const navigate = useNavigate();

  const [ButtonClick, setButtonClick] = useState(true);
  const handleButtonclick = () => {
    setButtonClick(false);
  };
  return (
    <div>
      <ModalOverlay style={{ display: isReservateOpen ? "flex" : "none" }}>
        <Container>
          {ButtonClick ? (
            <>
              <Purple>
                <h2>?</h2>
              </Purple>
              <img src="/images/call.png" alt="call"></img>
              <p>
                   .
                <br />
                    
              </p>
              <BtnWrapper>
                <CloseButton onClick={closeReservateModal}>
                  {" "}
                  {/*   */}
                  
                </CloseButton>
                <a href={`tel:${cliniccall}`}>
                  <SetButton onClick={handleButtonclick}> </SetButton>
                </a>
              </BtnWrapper>
            </>
          ) : (
            <>
              <Purple>
                <h2>?</h2>
              </Purple>
              <img src="/images/call.png" alt="call"></img>
              <p>
                    
                <br />
                    
              </p>
              <BtnWrapper>
                <CloseButton onClick={closeReservateModal}>
                  {" "}
                  {/*   */}
                  
                </CloseButton>
                <SetButton onClick={() => navigate("/calendar")}>
                   
                </SetButton>
              </BtnWrapper>
            </>
          )}
        </Container>
      </ModalOverlay>
    </div>
  );
}

const Container = styled.div`
  position: relative;
  width: 325px;
  height: 400px;
  background-color: white;
  border-radius: 30px;
  white-space: pre-line;
  z-index: 1000;

  img {
    width: 160px;
    display: block;
    margin-left: 90px;
    margin-top: 20px;
  }

  p {
    text-align: center;
    font-size: 15px;
  }
`;

export default ReservateModal;
