import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import "./MealFirst.css";

const MealFirst = () => {
  const navigate = useNavigate();
  const today = new Date();
  const week = ["", "", "", "", "", "", ""];
  const dayOfWeek = week[today.getDay()];
  const formattedDate = `${
    today.getMonth() + 1
  } ${today.getDate()} ${dayOfWeek}`;

  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  const [mealType, setMealType] = useState(1); // : 
  const [mealItems, setMealItems] = useState([]);
  const [inputValue, setInputValue] = useState("");

  const handleClickMeal = (e) => {
    const { value } = e.target;
    setMealType(Number(value)); //     
  };

  const handleInputChange = (e) => {
    setInputValue(e.target.value);
  };

  const handleAddMeal = (e) => {
    e.preventDefault();
    if (inputValue.trim()) {
      setMealItems((prevItems) => [
        ...prevItems,
        { id: Date.now(), name: inputValue.trim(), type: mealType }, // mealType 
      ]);
      setInputValue(""); //   
    }
  };

  const handleRemoveMeal = (id) => {
    setMealItems((prevItems) => prevItems.filter((item) => item.id !== id));
  };

  const handleNext = () => {
    navigate("/meal/second", { state: { mealType, mealItems } });
  };

  const renderMealItems = () => {
    return mealItems
      .filter((item) => item.type === mealType) //  mealType   
      .map((item) => (
        <div key={item.id} className="meal-item">
          <span>{item.name}</span>
          <button
            className="remove-button"
            onClick={() => handleRemoveMeal(item.id)}
          >
            x
          </button>
        </div>
      ));
  };

  return (
    <div className="MFbackground">
      <header className="blackHeader">
        <img alt="back" onClick={() => navigate(-1)} src="/images/back.png" />
        <h2 className="MFh2"> </h2>
      </header>
      <div className="MFdate">{formattedDate}</div>
      <main className="MFmain">
        <div className="MFmenu">
          <span className="MFpurpleDiv"></span>
          <button
            onClick={handleClickMeal}
            value="1"
            className={mealType === 1 ? "MFselected" : ""}
          >
            
          </button>
          <button
            onClick={handleClickMeal}
            value="2"
            className={mealType === 2 ? "MFselected" : ""}
          >
            
          </button>
          <button
            onClick={handleClickMeal}
            value="3"
            className={mealType === 3 ? "MFselected" : ""}
          >
            
          </button>
          <button
            onClick={handleClickMeal}
            value="4"
            className={mealType === 4 ? "MFselected" : ""}
          >
            
          </button>
        </div>
        <div className="MFinput">
          <form id="meal-form" onSubmit={handleAddMeal}>
            <input
              type="text"
              placeholder=" "
              value={inputValue}
              onChange={handleInputChange}
            />
            <button type="submit"></button>
          </form>
        </div>
        <hr />
        <div className="MFcontents">{renderMealItems()}</div>
        <div className="MFbtns">
          <button className="MFpurpleBtn" onClick={handleNext}>
            
          </button>
        </div>
      </main>
    </div>
  );
};

export default MealFirst;
