import React, { useEffect } from "react";
import { useNavigate } from "react-router-dom";
import "./Meal.css";

const Mealmain = () => {
  const navigate = useNavigate();
  const today = new Date();
  const week = ["", "", "", "", "", "", ""];
  let dayOfWeek = week[today.getDay()];
  const formattedDate = `${
    today.getMonth() + 1
  } ${today.getDate()} ${dayOfWeek}`;

  //      
  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  //    
  const BackButton = () => {
    navigate(-1);
  };

  // 
  const user = { name: "" };

  return (
    <div className="Mbackground">
      <header className="blackHeader">
        <img alt="back" onClick={BackButton} src="/images/back.png" />
        <h2 className="Mh2">{user.name}   </h2>
      </header>
      <main className="Mmain">
        <div className="Mdate">{formattedDate}</div>
        <div className="Minfo">
          <div className="Mback_purple">
            <span>  ?</span>
            <h2>
                <br />
              
            </h2>
            <img
              className="Mback_purple_img"
              alt=""
              src="/images/thinking.png"
            />
          </div>
          <div className="Mback_white">
            <img alt=" " src="/images/analysis_person.png" />
            <p>
              {" "}
                  
              <br />
                    <br />  
               
            </p>
          </div>
        </div>
        <img className="Marrow" alt="" src="/images/startdirection.png" />
        <div className="Mbtns">
          <button
            className="MpurpleBtn"
            onClick={() => navigate("/meal/first")}
          >
             
          </button>
        </div>
      </main>
    </div>
  );
};

export default Mealmain;
